<template>
  <div id="fabric-card-selectable-component">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(data.image_url + '@250-250')}')`
      }"
      @click="onImageClick"
    >
      <font-awesome-icon
        v-if="selected"
        class="icon"
        :icon="['fas', 'check-square']"
      ></font-awesome-icon>
      <font-awesome-icon
        v-else
        class="icon"
        :icon="['fas', 'square']"
      ></font-awesome-icon>
    </div>
    <div class="content-container">
      <div class="content-wrapper" :style="{ width: 'auto' }">
        <h3 class="title">{{ data.name }}</h3>
      </div>
      <div class="hover-information-wrapper">
        <p v-if="data.art_no" class="description">{{ data.art_no }}</p>
        <p v-if="data.sap_art_no" class="description">{{ data.sap_art_no }}</p>
        <p v-if="data.real_composition" class="description">
          {{ data.real_composition }}
        </p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { formatImageUrl } from "@/utils/image";
import { FabricCardSelectableEmit, FabricResult } from "@/typings/fabric";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object as PropType<FabricResult>,
      default: () => ({})
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const { data, selected } = toRefs(props);

    const onImageClick = async () => {
      const params: FabricCardSelectableEmit = {
        id: data.value.id,
        selected: selected.value,
        data: data.value
      };
      emit("select", params);
    };

    return {
      onImageClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

