<template>
  <div id="collection-card-selectable-component">
    <div class="cover-images-container" @click="onImageClick">
      <div class="cover-images-wrapper">
        <div class="col" v-for="(image, index) in images" :key="index">
          <div
            class="cover-image"
            :style="{
              backgroundImage: `url('${
                image ? formatImageUrl(image + '@150-150') : defaultImage
              }')`
            }"
          ></div>
        </div>
        <font-awesome-icon
          v-if="selected"
          class="icon"
          :icon="['fas', 'check-square']"
        ></font-awesome-icon>
        <font-awesome-icon
          v-else
          class="icon"
          :icon="['fas', 'square']"
        ></font-awesome-icon>
      </div>
    </div>

    <div class="content-container">
      <div class="content-wrapper" :style="{ width: 'auto' }">
        <h3 class="title">{{ data.name }}</h3>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType, computed } from "vue";
import * as _ from "ramda";
import { formatImageUrl } from "@/utils/image";
import { FabricCardSelectableEmit } from "@/typings/fabric";
import { CollectionCardResponse } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object as PropType<CollectionCardResponse>,
      default: () => ({})
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const { data, selected } = toRefs(props);

    const images = computed(() => {
      const _images = _.splitAt(4, data.value.fabric_images)[0];
      if (_images.length === 4) return _images;
      return _.splitAt(4, [..._images, null, null, null, null])[0];
    });

    const onImageClick = async () => {
      const params: FabricCardSelectableEmit = {
        id: data.value.id,
        selected: selected.value,
        data: data.value
      };
      emit("select", params);
    };

    return {
      images,

      onImageClick,

      formatImageUrl
    };
  }
});

export default Component;
</script>

