<template>
  <div id="collection-create-project-dialog-component">
    <el-dialog
      :title="getTitle(step)"
      v-model="visible"
      :width="getDialogWidth(step)"
      @closed="onDialogClosed"
      top="45px"
    >
      <!--Step 1-->
      <div class="step-1-container" v-if="step === 1">
        <h2>Where to start the project?</h2>
        <div class="buttons-wrapper">
          <el-button class="button" type="success" @click="onGoFabrics">
            Choose from Fabrics
          </el-button>
          <div class="line"></div>
          <el-button class="button" type="success" @click="onGoCollections">
            Choose from Collections
          </el-button>
          <el-button class="text-button" type="text" @click="onGoForm(true)">
            Choose Later
            <font-awesome-icon
              class="icon"
              :icon="['fas', 'chevron-right']"
            ></font-awesome-icon>
          </el-button>
        </div>
      </div>

      <!--Step 2 Choose collection-->
      <div class="step-2-container" v-if="step === 2">
        <el-row :gutter="60">
          <el-col v-for="item in collectionOptions" :key="item.id" :span="8">
            <CollectionCardSelectable
              :data="item"
              :selected="form.collection === item.id"
              @select="onCollectionSelect"
            ></CollectionCardSelectable>
          </el-col>
        </el-row>
      </div>

      <!--Step 3 Choose collection's fabric-->
      <div class="step-3-container" v-if="step === 3">
        <el-row :gutter="60">
          <el-col v-for="item in fabricOptions" :key="item.id" :span="6">
            <FabricCardSelectable
              :data="item"
              :selected="form.start_fabric === item.id"
              @select="onFabricSelect"
            ></FabricCardSelectable>
          </el-col>
        </el-row>
      </div>

      <!--Step 4 Choose fabric-->
      <div class="step-4-container" v-if="step === 4">
        <div class="search-bar-wrapper">
          <el-input
            placeholder="Search Fabric..."
            v-model="searchFabricValue"
            clearable
          >
            <template #prefix>
              <font-awesome-icon
                style="color: #444444; font-size: 18px;"
                :icon="['fas', 'search']"
              ></font-awesome-icon>
            </template>
          </el-input>
        </div>
        <el-row :gutter="60">
          <el-col v-for="item in fabricOptions" :key="item.id" :span="6">
            <FabricCardSelectable
              :data="item"
              :selected="form.start_fabric === item.id"
              @select="onFabricSelect"
            ></FabricCardSelectable>
          </el-col>
        </el-row>
        <div class="pagination-wrapper">
          <el-pagination
            v-if="pageCount"
            background
            layout="prev, pager, next"
            :current-page="page"
            :page-count="pageCount"
            @current-change="onPaginationChange"
          >
          </el-pagination>
        </div>
      </div>

      <!--Step 5 Enter form data-->
      <div class="step-5-container" v-if="step === 5">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Name</p>
            <el-input v-model="form.name"> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Expected Finish Time</p>
            <el-date-picker
              v-model="form.expected_end_at"
              value-format="YYYY-MM-DD"
              placeholder="Select Date"
              :disabledDate="datePickerDisabled"
            >
            </el-date-picker>
          </el-col>
          <el-col :span="24">
            <p class="label">Price (Optional)</p>
            <el-input v-model="form.price" type="number"></el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Brand</p>
            <el-select v-model="form.brand">
              <el-option
                v-for="item in brandOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <div class="label">Season</div>
            <el-select v-model="form.season" multiple placeholder="Select...">
              <el-option
                v-for="item in seasonOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Garment Factory (Optional)</p>
            <el-select v-model="form.garment_factory" clearable>
              <el-option
                v-for="item in garmentFactoryOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button
            v-if="step === 2 || step === 3 || step === 4"
            type="info"
            @click="onBack"
          >
            Back
          </el-button>

          <el-button
            v-if="step === 2"
            type="success"
            @click="onGoCollectionFabrics"
          >
            Next
          </el-button>
          <el-button
            v-if="step === 3 || step === 4"
            type="success"
            @click="onGoForm(false)"
          >
            Next
          </el-button>
          <el-button v-if="step === 5" type="success" @click="onConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, watch } from "vue";
import dayjs from "dayjs";
import CollectionCardSelectable from "@/components/CollectionCardSelectable.vue";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import { OptionItem } from "@/typings/asideFilter";
import { useRouter } from "vue-router";
import {
  FabricCardSelectableEmit,
  FabricResult,
  FabricsParams
} from "@/typings/fabric";
import { ProjectCreateInputs } from "@/typings/project";
import { getAvaliableOptions, postCreateProject } from "@/apis/project";
import { getCollection, getCollections } from "@/apis/collection";
import {
  CollectionCardResponse,
  CollectionFabrics,
  CollectionsParams
} from "@/typings/collection";
import { getFabrics } from "@/apis/fabric";
import { ElNotification } from "element-plus";
import { seasonOptions } from "@/views/Collection/options";

const getDialogWidth = (step: number) => {
  return ["", "30%", "80%", "80%", "80%", "40%"][step];
};

const getTitle = (step: number) => {
  return [
    "",
    "",
    "Choose Collection",
    "Choose Collection's fabric",
    "Choose Fabric",
    "Initiate Project"
  ][step];
};

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    CollectionCardSelectable,
    FabricCardSelectable
  },
  setup() {
    const router = useRouter();

    const visible = ref(false);
    const step = ref(1);
    const page = ref(1);
    const pageCount = ref(0);
    const datePickerDisabled = (date: Date) => {
      return dayjs(date).isBefore(dayjs());
    };

    const collectionOptions = ref<CollectionCardResponse[]>([]);
    const fabricOptions = ref<FabricResult[]>([]);
    const brandOptions = ref<OptionItem[]>([]);
    const garmentFactoryOptions = ref<OptionItem[]>([]);
    const searchFabricValue = ref("");
    const form = reactive<ProjectCreateInputs>({
      name: "",
      expected_end_at: dayjs()
        .add(1, "day")
        .format("YYYY-MM-DD"),
      price: 0,
      start_fabric: 0,
      brand: null,
      season: [],
      garment_factory: null,
      collection: null
    });

    const fetchOptions = async () => {
      const brandResponse = await getAvaliableOptions(
        "brand",
        form.collection,
        form.start_fabric
      );
      const garmentFactoryResponse = await getAvaliableOptions(
        "garment_factory"
      );
      brandOptions.value = brandResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
      garmentFactoryOptions.value = garmentFactoryResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
    };

    watch([searchFabricValue], async () => {
      const params: FabricsParams = { q: searchFabricValue.value };
      const { data } = await getFabrics(params);
      fabricOptions.value = data.results;
      page.value = 1;
      pageCount.value = data.total_pages;
    });

    const open = async () => {
      step.value = 1;
      visible.value = true;
    };

    const onGoCollections = async () => {
      step.value = 2;
      const params: CollectionsParams = {
        initiate_project: true
      };
      const { data } = await getCollections(params);
      collectionOptions.value = data;
    };

    const onGoCollectionFabrics = async () => {
      if (!form.collection) {
        return ElNotification({
          type: "warning",
          title: "Warning",
          message: "You should choose a collection to continue."
        });
      }
      searchFabricValue.value = "";
      step.value = 3;
      const { data } = await getCollection(form.collection.toString());
      fabricOptions.value = data.collection_fabrics.map(
        (it: CollectionFabrics) => it.fabric
      );
    };

    const onGoFabrics = async () => {
      step.value = 4;
      const params: FabricsParams = {};
      const { data } = await getFabrics(params);
      fabricOptions.value = data.results;
      page.value = 1;
      pageCount.value = data.total_pages;
    };

    const onCollectionSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      form.collection = data.id;
    };

    const onFabricSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      form.start_fabric = data.id;
    };

    const onPaginationChange = async (_page: number) => {
      const params: FabricsParams = { page: _page };
      const { data } = await getFabrics(params);
      fabricOptions.value = data.results;
      page.value = _page;
      pageCount.value = data.total_pages;
    };

    const onGoForm = async (later: boolean) => {
      if (!later && !form.start_fabric) {
        return ElNotification({
          type: "warning",
          title: "Warning",
          message: "You should fabric a collection to continue."
        });
      }
      step.value = 5;
      await fetchOptions();
    };

    const onConfirm = async () => {
      if (!form.name || !form.expected_end_at || !form.brand) {
        return ElNotification({
          title: "Error",
          type: "error",
          message: `Complete the required fields to initiate the project.`
        });
      }
      const _form: ProjectCreateInputs = {
        name: form.name,
        expected_end_at: form.expected_end_at,
        brand: form.brand,
        season: form.season,
        garment_factory: form.garment_factory
      };
      if (form.start_fabric) _form.start_fabric = form.start_fabric;
      if (form.price) _form.price = form.price;
      const { data } = await postCreateProject(_form);
      visible.value = false;
      await router.push({ name: "ProjectDetail", params: { id: data.id } });
    };

    const onBack = async () => {
      form.collection = null;
      form.start_fabric = 0;
      step.value = 1;
    };

    // Initial
    const onDialogClosed = () => {
      step.value = 1;
      page.value = 1;
      pageCount.value = 0;
      collectionOptions.value = [];
      fabricOptions.value = [];
      brandOptions.value = [];
      garmentFactoryOptions.value = [];
      searchFabricValue.value = "";
      form.name = "";
      form.expected_end_at = dayjs()
        .add(1, "day")
        .format("YYYY-MM-DD");
      form.price = 0;
      form.start_fabric = 0;
      form.brand = null;
      form.season = [];
      form.garment_factory = null;
      form.collection = null;
    };

    return {
      visible,
      step,
      page,
      pageCount,
      form,
      searchFabricValue,
      collectionOptions,
      fabricOptions,
      brandOptions,
      seasonOptions,
      garmentFactoryOptions,

      open,
      onGoCollections,
      onGoCollectionFabrics,
      onGoFabrics,
      onCollectionSelect,
      onFabricSelect,
      onPaginationChange,
      onGoForm,
      onConfirm,
      onBack,
      onDialogClosed,

      dayjs,
      datePickerDisabled,
      getDialogWidth,
      getTitle
    };
  }
});

export default Component;
</script>

