<!--suppress ALL -->
<template>
  <div id="projects-component">
    <div class="top-container">
      <el-input
        class="input"
        v-model="qValue"
        placeholder="Search project..."
      ></el-input>
      <el-button
        v-permission="'project_add'"
        type="success"
        @click="onCreateClick"
      >
        Initiate Project
      </el-button>
    </div>

    <div class="recent-projects-container">
      <h2 class="zoom-title">Recent Projects</h2>
      <div class="row">
        <div class="col" v-for="item in recentList" :key="item.id">
          <el-card
            :body-style="{ padding: 0 }"
            class="card"
            shadow="hover"
            @click="onProjectClick(item.id)"
          >
            <div
              class="cover"
              :style="{
                backgroundImage: `url('${formatImageUrl(
                  item.display_fabric_image_path
                ) + '@250-250'}')`
              }"
            ></div>
            <div class="card-name">
              <p>{{ item.name }}</p>
            </div>
          </el-card>
          <div v-if="item.unread_count" class="ccard-count-dot">
            {{ item.unread_count }}
          </div>
        </div>
      </div>
    </div>

    <div class="all-project-card">
      <el-tabs class="tabs" v-model="tabValue">
        <el-tab-pane
          v-for="tab in tabOptions"
          :key="tab.value"
          :label="tab.label"
          :name="tab.value"
        >
        </el-tab-pane>
      </el-tabs>
      <el-row v-if="list.length" :gutter="30">
        <el-col v-for="item in list" :key="item.id" :span="8">
          <ProjectCard :data="item" @card-click="onProjectClick"></ProjectCard>
        </el-col>
      </el-row>
      <div v-else class="empty">
        <p>No Data</p>
      </div>
      <div class="pagination-wrapper">
        <el-pagination
          v-if="pageCount > 1"
          background
          layout="prev, pager, next"
          :current-page="pageValue"
          :page-count="pageCount"
          @current-change="onPaginationChange"
        >
        </el-pagination>
      </div>
    </div>
    <CreateProjectDialog ref="createProjectDialog"></CreateProjectDialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted, Ref, watch } from "vue";
import { useRouter } from "vue-router";

import ProjectCard from "./ProjectCard.vue";
import CreateProjectDialog from "./CreateProjectDialog.vue";

import { loading } from "@/utils";
import { formatImageUrl } from "@/utils/image";
import { getProjects } from "@/apis/project";
import { Project, ProjectsParams } from "@/typings/project";
import { iCreateDialog } from "@/typings/component";
import { OptionItem } from "@/typings/asideFilter";

const tabOptions: OptionItem[] = [
  { label: "All", value: "All", checked: false },
  { label: "In Progress", value: "In Progress", checked: false },
  { label: "Completed", value: "Completed", checked: false },
  { label: "Delayed", value: "Delayed", checked: false },
  { label: "Dropped", value: "Dropped", checked: false }
];

const Component = defineComponent({
  name: "Projects",
  components: {
    CreateProjectDialog,
    ProjectCard
  },
  setup() {
    const router = useRouter();

    const createProjectDialog: Ref<iCreateDialog | null> = ref(null);
    const tabValue = ref("All");
    const qValue = ref("");
    const recentList = ref<Project[]>([]);
    const list = ref<Project[]>([]);
    const pageCount = ref(0);
    const pageValue = ref(1);
    const lock = ref(false);

    const fetchData = async (page?: number) => {
      if (!page) {
        pageValue.value = 1;
      }

      const params: ProjectsParams = {
        page: pageValue.value
      };

      if (tabValue.value !== "All") {
        params.status = tabValue.value;
      }

      if (qValue.value) {
        params.q = qValue.value;
      }

      const { data } = await getProjects(params);

      list.value = data.results;
      pageCount.value = data.total_pages;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        const { data } = await getProjects({
          order_by: "-modified_at",
          limit: 5
        });
        recentList.value = data.results;
        await fetchData();
      } finally {
        loadingInstance.close();
      }
    };

    watch([tabValue, qValue], async () => {
      await fetchData();
    });

    onMounted(init);

    const onProjectClick = async (id: number) => {
      await router.push({
        name: "ProjectDetail",
        params: { id }
      });
    };

    const onPaginationChange = (page: number) => {
      pageValue.value = page;
      fetchData(page);
    };

    const onCreateClick = () => {
      if (!createProjectDialog.value) return;
      createProjectDialog.value.open();
    };

    return {
      createProjectDialog,
      recentList,
      list,
      tabOptions,
      tabValue,
      qValue,
      pageCount,
      pageValue,

      onPaginationChange,
      onCreateClick,
      onProjectClick,

      formatImageUrl
    };
  }
});

export default Component;
</script>

