<template>
  <div id="project-card-component" @click="onClick">
    <div class="top-container">
      <div class="left-container">
        <time class="date">{{
          dayjs(data.created_at).format("MM DD, YYYY")
        }}</time>
        <el-tag effect="dark" size="small" :color="getTagColor(data.status)">
          {{ data.status }}
        </el-tag>
        <h2 class="title">
          {{ data.name }}
        </h2>
        <p>{{ data.newest_stage_name }}</p>
      </div>
      <div class="right-container">
        <img
          v-if="data.display_fabric_image_path"
          :src="formatImageUrl(data.display_fabric_image_path) + '@100-100'"
          :alt="data.name"
        />
        <div v-else class="empty-box"></div>
      </div>
    </div>
    <div class="progress-container">
      <el-progress
        :stroke-width="15"
        :percentage="data.progress * 100"
        :text-inside="true"
        :status="data.progress <= 1 ? 'success' : 'exception'"
      >
        <span></span>
      </el-progress>
    </div>
    <div class="bottom-container">
      <Avatars :max="4" :data="data.joined_member"></Avatars>
      <div class="progress-text">
        <p v-if="data.progress > 1">Delayed</p>
        <p v-else>Time Consumed {{ Math.round(data.progress * 100) }}%</p>
      </div>
    </div>
    <div v-if="data.unread_count" class="count-dot">
      {{ data.unread_count }}
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import dayjs from "dayjs";
import Avatars from "@/components/Avatars.vue";
import { Project } from "@/typings/project";
import { formatImageUrl } from "@/utils";

const colors = ["#5470c6", "#91cc76", "#f8c858", "#e66"];
const getTagColor = (tag: string): string => {
  const adapter: Adapter<string> = {
    "In Progress": colors[0],
    "Completed but delayed": colors[1],
    Dropped: colors[3],
    Delayed: colors[2],
    default: ""
  };
  return adapter[tag] || adapter.default;
};

const Component = defineComponent({
  name: "ProjectCard",
  props: {
    data: {
      type: Object as PropType<Project>,
      default: () => ({})
    }
  },
  components: {
    Avatars
  },
  setup(props, { emit }) {
    const { data } = toRefs(props);

    const onClick = () => {
      emit("card-click", data.value.id);
    };

    return {
      onClick,

      getTagColor,
      dayjs,
      formatImageUrl
    };
  }
});

export default Component;
</script>

